___CSS_LOADER_EXPORT___.push([module.id, `* {
    font-family: sans-serif;
}

header *,
dialog * {
    font-size: min(3vw, 3vh, 20px);
}

html,
body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    height: 100%;
    width: 100%;
}

body {
    background: linear-gradient(0.25turn, #BBCCEE, #CCEEFF, #CCDDAA, #EEEEBB, #FFCCCC);
}

header {
    height: 2em;
    width: 100%;
    background: #FFF8;
    display: flex;
    gap: 1em;
    justify-content: center;
    align-items: center;
}

#computation-animation {
    height: 100%;
    width: 100%;
    background: #888;
    opacity: 50%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity 0.125s;
    z-index: 10;
}

#computation-animation.invisible {
    display: none;
    opacity: 0;
}

#computation-animation>div.animation-block {
    animation: animation-block;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    width: min(12.5vw, 12.5vh);
    height: min(12.5vw, 12.5vh);
    border-radius: min(1.5625vw, 1.5625vh);
    position: absolute;
}

@keyframes animation-block {
    0% {
        translate: -50% -50%;
    }

    10%,
    25% {
        translate: 50% -50%;
    }

    35%,
    50% {
        translate: 50% 50%;
    }

    60%,
    75% {
        translate: -50% 50%;
    }

    85%,
    100% {
        translate: -50% -50%;
    }
}

#svg-puzzle-container,
#svg-puzzle {
    height: 100%;
    width: 100%;
    max-width: 100vw;
    max-height: calc(100vh - 2em);
}

#svg-puzzle>path.dragging {
    filter: contrast(1.5);
}

#svg-puzzle>path.shadowGoal {
    fill-opacity: 0.25;
    stroke-opacity: 0.5;
}

#change-puzzle-dialog {
    border-radius: 0.5em;
}

#change-puzzle-dialog-container {
    display: flex;
    flex-direction: column;
    gap: 1em;
    width: 20em;
    align-items: center;
}

#puzzle-dialog-buttons {
    display: flex;
    flex-direction: row;
    gap: 1em;
}

#error-msg {
    color: darkred;
}

.puzzle-string {
    font-family: monospace;
}`, "",{"version":3,"sources":["webpack://./style.css"],"names":[],"mappings":"AAAA;IACI,uBAAuB;AAC3B;;AAEA;;IAEI,8BAA8B;AAClC;;AAEA;;IAEI,SAAS;IACT,UAAU;IACV,gBAAgB;IAChB,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,kFAAkF;AACtF;;AAEA;IACI,WAAW;IACX,WAAW;IACX,iBAAiB;IACjB,aAAa;IACb,QAAQ;IACR,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,gBAAgB;IAChB,YAAY;IACZ,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,0BAA0B;IAC1B,WAAW;AACf;;AAEA;IACI,aAAa;IACb,UAAU;AACd;;AAEA;IACI,0BAA0B;IAC1B,sBAAsB;IACtB,mCAAmC;IACnC,0BAA0B;IAC1B,2BAA2B;IAC3B,sCAAsC;IACtC,kBAAkB;AACtB;;AAEA;IACI;QACI,oBAAoB;IACxB;;IAEA;;QAEI,mBAAmB;IACvB;;IAEA;;QAEI,kBAAkB;IACtB;;IAEA;;QAEI,mBAAmB;IACvB;;IAEA;;QAEI,oBAAoB;IACxB;AACJ;;AAEA;;IAEI,YAAY;IACZ,WAAW;IACX,gBAAgB;IAChB,6BAA6B;AACjC;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;AACZ;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,sBAAsB;AAC1B","sourcesContent":["* {\r\n    font-family: sans-serif;\r\n}\r\n\r\nheader *,\r\ndialog * {\r\n    font-size: min(3vw, 3vh, 20px);\r\n}\r\n\r\nhtml,\r\nbody {\r\n    margin: 0;\r\n    padding: 0;\r\n    overflow: hidden;\r\n    height: 100%;\r\n    width: 100%;\r\n}\r\n\r\nbody {\r\n    background: linear-gradient(0.25turn, #BBCCEE, #CCEEFF, #CCDDAA, #EEEEBB, #FFCCCC);\r\n}\r\n\r\nheader {\r\n    height: 2em;\r\n    width: 100%;\r\n    background: #FFF8;\r\n    display: flex;\r\n    gap: 1em;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n#computation-animation {\r\n    height: 100%;\r\n    width: 100%;\r\n    background: #888;\r\n    opacity: 50%;\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    transition: opacity 0.125s;\r\n    z-index: 10;\r\n}\r\n\r\n#computation-animation.invisible {\r\n    display: none;\r\n    opacity: 0;\r\n}\r\n\r\n#com